body {
  margin: 0;
  background-image: url(../../static/media/Pattern.9a713fab.png);
  display:flex;
  flex-direction: column;
  align-items: center;
}
#root{
  margin:0px;
  padding:0px;
}

h1{
  text-align: center;
  text-shadow: 0 1px 0 #000000;
  color: #ffffff;
  font-family: "Exo 2.0- Thin";
  font-size: 2.25rem;
  margin-top:36px;
  font-weight: 400;
  margin-bottom: 26px;
}


.app{
  margin-top: 0%;
  height: 100vh;
  width: 100vw;
}

.Goodlist{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left:3.125vw;
  padding-right:3.125vw;
}
.good{
  margin-left: 3.12vw;
  margin-right:3.12vw;
  height: 509px;
  width: 320px;
}

@media screen and (max-width:1279px){
  
  .Goodlist{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left:1%;
    padding-right:1%;
  }

  .good{
    margin-left: 1%;
    margin-right: 1%;
  }
}

@media screen and (max-width:1023px){
  .Goodlist{
    flex-direction: column;
  }
  .good{
    margin-bottom: 20px;
  }
  .app{
    height: 100%;
  }
}



